import React, { useState } from "react";
import { render } from "react-dom";
import "antd/dist/antd.css";
import axios from "axios";
import { Upload } from "antd";
import ImgCrop from "antd-img-crop";




const UploadCropBox = (props) => {
    const [defaultFileList, setDefaultFileList] = useState([]);

    const uploadImage = async options => {
        let shouldRender = false;
        const { onSuccess, file } = options;

        const formData = new FormData();
        const config = {
            headers: { "content-type": "multipart/form-data" },
        };
        formData.append("image", file);
        axios.defaults.timeout = 30000;
        try {
            // const res = await axios.post(
            //     'http://60.205.169.15:28934?meta_data={"image_size":64}',
            //     formData,
            //     config
            // );
            const res = await axios.post(
                'http://localhost.charlesproxy.com:3000/api/mock',
            )

            onSuccess("Ok");


            let depth = res.data.depth;
            let albedo = res.data.albedo;
            let env_light = res.data.env_light;
            let diffuse_light = res.data.diffuse_light;
            let indices = res.data.index;
            let normals = res.data.normals;

            props.parent.getRenderInfo(depth, albedo, env_light, diffuse_light, indices, normals);


            shouldRender = true;
            props.parent.getRenderStatus(shouldRender);
            console.log("Rcv response after upload")

            //console.log("res: ", res.data);
        } catch (err) {
            console.log("Error: ", err);
        }
    };

    const handleOnChange = ({ file, fileList, event }) => {
        setDefaultFileList(fileList);
    };

    return (
        <div style={{display:"block", marginLeft:20, marginRight:"auto", width:"40%"}}>
        <ImgCrop
            rotate
            modalTitle = "Please clip the image to maximize the facial part"
            grid={true}
            quality={0.8}
            modalOk={"Submit"}
        >
            <Upload
                style={{marginLeft:20}}
                accept="image/*"
                customRequest={uploadImage}
                onChange={handleOnChange}
                listType="picture-card"
                defaultFileList={defaultFileList}
                className="image-upload-grid"
            >
                {defaultFileList.length >= 1 ? null : <div>+ Upload</div>}
            </Upload>
        </ImgCrop>
        </div>
    );
};
export default UploadCropBox;
